.experience-card-compact{background:rgba(20,20,23,.8);backdrop-filter:blur(10px);border:1px solid hsla(0,0%,100%,.08);border-radius:12px;padding:25px;margin-bottom:25px;width:100%;display:flex;transition:all .3s ease;position:relative;overflow:hidden;box-sizing:border-box}.experience-card-compact:hover{transform:translateY(-3px);border-color:#00f3ff;box-shadow:0 10px 30px rgba(0,0,0,.4)}.experience-card-compact:hover .timeline-line{background:#00f3ff;box-shadow:0 0 8px #00f3ff}.experience-card-left{display:flex;flex-direction:column;align-items:center;margin-right:25px;flex-shrink:0}.company-logo-wrapper{width:65px;height:65px;background:#fff;border-radius:10px;padding:5px;display:flex;justify-content:center;align-items:center;z-index:2}.company-logo{width:100%;height:100%;object-fit:contain}.timeline-line{width:2px;flex-grow:1;background:hsla(0,0%,100%,.1);margin-top:10px;border-radius:2px;transition:all .3s}.experience-card-right{flex-grow:1;min-width:0}.card-header{display:flex;justify-content:space-between;align-items:flex-start;margin-bottom:10px;flex-wrap:wrap;gap:10px}.role-title{font-size:1.3rem;font-weight:700;color:#fff;margin:0;line-height:1.2;word-wrap:break-word}.company-name{font-size:1rem;color:#00f3ff;font-family:"Fira Code",monospace;margin-top:4px;font-weight:600}.date-badge{font-size:.85rem;color:#aaa;background:hsla(0,0%,100%,.05);padding:4px 10px;border-radius:4px;font-family:"Fira Code",monospace;white-space:nowrap}.job-desc{color:#f0f0f0;font-size:.95rem;line-height:1.6;margin-bottom:12px;opacity:.9}.job-bullets{padding:0;margin:0;list-style:none !important}.bullet-item{list-style:none !important;position:relative;padding-left:20px;color:#ccc;margin-bottom:5px;font-size:.9rem;word-wrap:break-word}.bullet-item::before{content:"›" !important;position:absolute;left:0;top:0;color:#39ff14;font-weight:bold;font-size:1.2rem;line-height:1.2rem}@media(max-width: 768px){.experience-card-compact{flex-direction:column;padding:20px}.experience-card-left{flex-direction:row;margin-bottom:15px;margin-right:0;width:100%;align-items:center;border-bottom:1px solid hsla(0,0%,100%,.05);padding-bottom:15px}.timeline-line{display:none}.logo-wrapper{width:50px;height:50px;margin-right:15px}.card-header{flex-direction:column;align-items:flex-start}.date-badge{margin-left:0;margin-top:5px}.role-title{font-size:1.2rem}}