.education-card-compact{background:rgba(20,20,23,.75);backdrop-filter:blur(10px);border:1px solid hsla(0,0%,100%,.08);border-radius:12px;padding:25px;margin-bottom:25px;width:100%;display:flex;transition:all .3s ease;position:relative;overflow:hidden;box-sizing:border-box}.education-card-compact:hover{transform:translateY(-3px);border-color:#bd00ff;box-shadow:0 10px 40px rgba(0,0,0,.5)}.education-card-compact:hover .vertical-line{background:#bd00ff;box-shadow:0 0 8px #bd00ff}.education-card-left{display:flex;flex-direction:column;align-items:center;margin-right:25px;flex-shrink:0}.logo-wrapper{width:70px;height:70px;background:#fff;border-radius:12px;padding:5px;display:flex;justify-content:center;align-items:center;z-index:2}.education-logo{width:100%;height:100%;object-fit:contain}.vertical-line{width:2px;flex-grow:1;background:hsla(0,0%,100%,.1);margin-top:10px;border-radius:2px;transition:all .3s}.education-card-right{flex-grow:1;min-width:0}.card-header{display:flex;justify-content:space-between;align-items:flex-start;margin-bottom:10px;flex-wrap:wrap;gap:10px}.school-name{font-size:1.3rem;font-weight:700;color:#fff;margin:0;line-height:1.2}.degree-name{font-size:1rem;color:#bd00ff;font-family:"Montserrat",sans-serif;font-weight:600;margin-top:4px}.date-badge{font-size:.85rem;color:#aaa;font-family:"Fira Code",monospace;background:hsla(0,0%,100%,.05);padding:4px 10px;border-radius:4px;white-space:nowrap}.education-bullets{padding:0;margin:0;list-style:none !important}.bullet-item{list-style:none !important;padding-left:20px;position:relative;color:#ccc;margin-bottom:5px;font-size:.9rem}.bullet-item::before{content:"✓" !important;position:absolute;left:0;color:#bd00ff;font-weight:bold;font-size:1rem}@media(max-width: 768px){.education-card-compact{flex-direction:column;padding:20px}.education-card-left{flex-direction:row;margin-bottom:15px;margin-right:0;width:100%;border-bottom:1px solid hsla(0,0%,100%,.05);padding-bottom:15px}.vertical-line{display:none}.logo-wrapper{width:50px;height:50px;margin-right:15px}.card-header{flex-direction:column;align-items:flex-start}.date-badge{margin-left:0;margin-top:5px}}